.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: #8db9e0;
}

.breed-select {
  width: 350px;
  height: 40px;
  color: #9b1a6a;
  letter-spacing: 4px;
  background-color: #cef7f7;
  border-radius: 5px;
  margin: 20px;
  font-size: 18px;
  font-weight: 700;
  box-shadow: inset 0 0 20px rgba(0, 0, 0, .5);
}

.cat-info {
  margin: 20px;
}

p.error {
  color: red;
  text-shadow: 0 0 1px rgba(0, 0, 0, .5), 1px 1px 2px rgba(0, 0, 0, .8);
  margin: 20px;
  font-size: 18px;
  font-weight: 700;
}

p.loader {
  margin: 20px;
  font-size: 18px;
  font-weight: 700;
}

.cat-info {
  background-color: #f0f8ff;
  border-radius: 5px;
  padding: 10px;
  display: flex;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, .3), inset 0 0 10px rgba(0, 0, 0, .5);
}

.pic {
  border-radius: 5px;
}

.info-box {
  padding: 15px;
}

.title {
  text-align: center;
}

.article {
  text-indent: 5%;
  font-size: 22px;
  line-height: 1.5;
}

.loaders {
  box-sizing: border-box;
  width: 50px;
  height: 80px;
  background: linear-gradient(#ff3d00 30px, rgba(0, 0, 0, 0) 0) 50% 0 / 2px 40px no-repeat;
  border-top: 5px solid #fff;
  border-bottom: 5px solid #fff;
  animation: spinx 5s linear infinite;
  display: inline-block;
  position: relative;
}

.loaders:before, .loaders:after {
  content: "";
  width: 40px;
  height: 35px;
  background: rgba(255, 255, 255, .4) 0 0 / 100% no-repeat;
  border-radius: 0 0 20px 20px;
  animation: lqt 5s linear infinite;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.loaders:after {
  border-radius: 20px 20px 0 0;
  animation: lqb 5s linear infinite;
  top: auto;
  bottom: 0;
}

@keyframes lqt {
  0%, 100% {
    background-image: linear-gradient(#ff3d00 40px, rgba(0, 0, 0, 0) 0);
    background-position: 0 0;
  }

  50% {
    background-image: linear-gradient(#ff3d00 40px, rgba(0, 0, 0, 0) 0);
    background-position: 0 40px;
  }

  50.1% {
    background-image: linear-gradient(#ff3d00 40px, rgba(0, 0, 0, 0) 0);
    background-position: 0 -40px;
  }
}

@keyframes lqb {
  0% {
    background-image: linear-gradient(#ff3d00 40px, rgba(0, 0, 0, 0) 0);
    background-position: 0 40px;
  }

  100% {
    background-image: linear-gradient(#ff3d00 40px, rgba(0, 0, 0, 0) 0);
    background-position: 0 -40px;
  }
}

@keyframes spinx {
  0%, 49% {
    background-position: 50% 36px;
    transform: rotate(0deg);
  }

  51%, 98% {
    background-position: 50% 4px;
    transform: rotate(180deg);
  }

  100% {
    background-position: 50% 36px;
    transform: rotate(360deg);
  }
}

